  .links {
    color: #fff;
    // padding: 0 40px;
    flex-grow: 1;
    display: flex;

    .link {
      padding: 5px;
      font-size: 1rem;
      font-weight: 500;
      opacity: 0.8;

      &:hover {
        cursor: pointer;
        opacity: 1;
      }
    }
  }

  .connect-wallet {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    text-overflow: ellipsis;
    color: #fff;

    .user-profile {
      display: flex;
      align-items: center;

      &:hover {
        cursor: pointer;
      }

      .pfp {
        width: 32px;
        height: 32px;
        border-radius: 16px;
      }

      .user-name {
        margin-left: 10px;
        font-weight: 500;
        max-width: 120px;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
      }
    }

    .connect-btn {
      min-width: 100px;
      padding: 8px 16px;
    }
  }
}

